import api from '../../service/api'
import { addNotification as notify } from 'reapop'

const SET_USER = '@@chat/SET_USER'
const SET_USERNAME = '@@chat/SET_USERNAME'
const SET_TOKEN = '@@chat/SET_TOKEN'

export const tokenReducer = (state = null, action = {}) => {
  switch (action.type) {
    case SET_TOKEN:
      return action.token

    default:
      return state
  }
}

export const userReducer = (state = null, action = {}) => {
  switch (action.type) {
    case SET_USER:
      return action.user

    case SET_USERNAME:
      return {...state, username: action.username}

    default:
      return state
  }
}

export const signIn = (username, password) => dispatch => {
  api.signIn(username, password)
     .then(response => {
       dispatch({type: SET_USER, user: response.user})
       dispatch({type: SET_TOKEN, token: response.token})
       return response
     })
     .catch(error => {
       dispatch(notify({
         message: error.message,
         status: 'error',
         dismissible: true,
         dismissAfter: 4000,
       }))
     })
}

export const signUp = (username, password) => dispatch => {
  api.signUp(username, password)
     .then(response => {
       dispatch({type: SET_USER, user: response.user})
       dispatch({type: SET_TOKEN, token: response.token})
     })
     .catch(error => {
       dispatch(notify({
         message: error.message,
         status: 'error',
         dismissible: true,
         dismissAfter: 4000,
       }))
     })
}

export const rename = (username) => dispatch => {
  api.rename(username)
     .then(response => {
       dispatch({type: SET_USERNAME, username})
       return response
     })
     .catch(error => {
       dispatch(notify({
         message: error.message,
         status: 'error',
         dismissible: true,
         dismissAfter: 4000,
       }))
     })
}



