import React from 'react'
import Rodal from 'rodal'
import 'rodal/lib/rodal.css'

export class Popup extends React.Component {
  static defaultProps = {
    width: 300,
    height: 220,
    color: '#fff',
    textAlign: 'center',
    background: '#0f1a22',
    borderRadius: '7px',
    showCloseButton: true,
  }

  render = () => (
    <Rodal {...this.props} >
      {this.props.children}
    </Rodal>
  )
}



