import React, { Fragment } from 'react'
import { connect } from 'react-redux'
import './styles.css'
import { SidePanel } from './side-panel'
import { appendMessage } from './redux'

const Message = ({data}) => (
  <li className="sent">
    <img src="http://emilcarlsson.se/assets/mikeross.png" alt="" />
    <div className='username'>{data.from.username}:</div>
    <p>{data.body}</p>
  </li>
)

@connect(state => ({
  user: state.user,
  token: state.token,
  messages: state.messages,
  active_chat: state.active_chat,
}), {appendMessage})
export class Chat extends React.Component {
  constructor (props) {
    super(props)

    this.socket = null
    this.state = {
      text: '',
    }
  }

  componentDidMount = () => {
    const url = 'wss://chattoo.heygaben.com/private/ws?itok=' + this.props.token
    this.socket = new WebSocket(url)
    this.socket.onmessage = this.handleMessage
  }

  handleMessage = (event) => {
    const message = JSON.parse(event.data)
    this.props.appendMessage(message)
  }

  componentWillUnmount () {
    this.socket.close()
  }

  render = () => (
    <div id="frame">
      <SidePanel user={this.props.user} />
      <div className="content">
        {
          this.props.active_chat &&
          <Fragment>
            <div className="contact-profile">
              <img src="http://emilcarlsson.se/assets/harveyspecter.png" alt="" />
              <p>{this.props.active_chat.username}</p>
            </div>

            <MessageList messages={this.props.messages}
                         active_chat={this.props.active_chat}
                         user={this.props.user}
            />

            {this.socket && <SendWidget value={this.state.text} socket={this.socket} />}
          </Fragment>
        }
      </div>
    </div>
  )
}

@connect(state => ({
  user: state.user,
  active_chat: state.active_chat,
}), {appendMessage})
class SendWidget extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      text: '',
    }
  }

  handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.sendMessage()
    }
  }

  onMessageChange = (e) => {
    this.setState({text: e.target.value})
  }

  sendMessage = () => {
    if (this.props.socket.readyState !== this.props.socket.OPEN) {
      return null
    }

    if (this.state.text === '') {
      return null
    }

    switch (this.props.active_chat.id) {
      case 'public':
        this.props.socket.send(JSON.stringify({
          type: 'public',
          from: this.props.user,
          to: null,
          body: this.state.text,
        }))
        break

      default:
        this.props.socket.send(JSON.stringify({
          type: 'private',
          from: this.props.user,
          to: this.props.active_chat,
          body: this.state.text,
        }))
    }

    this.setState({text: ''})
  }

  render = () => (
    <div className="message-input">
      <div className="wrap">
        <input placeholder="Write your message..."
               value={this.state.text}
               onKeyPress={this.handleKeyPress}
               onChange={this.onMessageChange} />

        <button className="submit" onClick={this.sendMessage}>
          <i className="fa fa-paper-plane" aria-hidden="true" />
        </button>
      </div>
    </div>
  )
}

const MessageList = ({messages, active_chat, user}) => (
  <div className="messages">
    <ul>
      {
        active_chat.id === 'public'
          ? messages.filter(message => message.type === 'public')
                    .map(message => <Message data={message} />)

          : messages.filter(message => message.type === 'private')
                    .filter(message => (message.from.id === active_chat.id) ||
                      (message.from.id === user.id && message.to && message.to.id === active_chat.id))
                    .map(message => <Message data={message} />)
      }
    </ul>
  </div>
)



