import qs from 'querystring'

class Http {
  constructor () {
    this.token = null
    this.csrf = null
  }

  setAuthHeader = token => {
    this.token = 'Bearer ' + token
  }

  setCSRFHeader = token => {
    this.csrf = token
  }

  /**
   * Shortcut for GET request.
   * @param url
   */
  get = (url, query) => {
    if (!query) {
      return this.client(url)
    }

    return this.client(url + '?' + qs.stringify(query))
  }

  /**
   * Shortcut for POST request.
   * @param url
   * @param data
   */
  post = (url, data = {}) => {
    return this.client(url, {
      method: 'POST',
      body: typeof data === 'object' ? JSON.stringify(data) : data,
    })
  }

  /**
   * @private
   * @param uri
   * @param options
   * @returns {Promise<Response>}
   */
  client = (uri, options = {credentials: 'same-origin'}) => {
    if (typeof fetch !== 'function') {
      throw new TypeError('fetch() function not available')
    }

    let headers = {'content-type': 'application/json'}
    if (this.token) {
      headers = {'Authorization': this.token, ...headers}
    }

    if (this.csrf) {
      headers = {'X-CSRF-Token': this.csrf, ...headers}
    }

    options = {headers, ...options}

    return fetch(uri, options)
      .then(this.preprocess)
    // .catch(error => Promise.reject(error))
  }
  /**
   * @private
   * @param response
   */
  preprocess = response => {
    const contentType = response.headers.get('content-type')

    if (response.status !== 200) {
      return response.json().then(response => {
        throw new TypeError(response.message)
      })
    }

    if (contentType && contentType.includes('application/json')) {
      return response.json()
    }

    console.log('Broken JSON response! Status code: ' + response.status)
    return response
  }
}

export default new Http()



