import React from 'react'
import { fetchContacts, setActiveChat } from './redux'
import { connect } from 'react-redux'
import { RenameButton } from '../user/rename'

const Profile = ({user}) => (
  <div id="profile">
    <div className="wrap">
      <img id="profile-img"
           src="http://emilcarlsson.se/assets/mikeross.png"
           className="online" alt="" />
      <p>{user.username} <RenameButton user={user} /></p>
    </div>
  </div>
)

@connect(state => ({
  user: state.user,
  contacts: state.contacts,
  active_chat: state.active_chat,
  messages: state.messages,
}), {fetchContacts, setActiveChat})
export class SidePanel extends React.Component {
  static public = 'public'

  componentDidMount = () => {
    this.props.fetchContacts()
  }

  setActive = (user) => {
    this.props.setActiveChat(user)
  }

  render = () => (
    <div id="sidepanel">
      <Profile user={this.props.user} />
      <div id="contacts">
        <ul>
          <Contact onClick={e => this.setActive({id: SidePanel.public, username: SidePanel.public})}
                   username={SidePanel.public}
                   active={this.props.active_chat && this.props.active_chat.id === SidePanel.public} />
          {
            this.props.contacts
                .filter(contact => contact.id !== this.props.user.id)
                .map(contact => (
                  <Contact onClick={e => this.setActive(contact)}
                           key={contact.id}
                           id={contact.id}
                           username={contact.username}
                           active={this.props.active_chat && this.props.active_chat.id === contact.id}
                  />),
                )
          }
        </ul>
      </div>
    </div>
  )
}

@connect(state => ({
  user: state.user,
  active_chat: state.active_chat,
}))
class Contact extends React.Component {
  render = () => {
    const {active, username} = this.props
    return (
      <li className={`contact ${active ? 'active' : ''}`}
          onClick={this.props.onClick}>
        <div className="wrap">
          <span className="contact-status" />
          <img src="http://emilcarlsson.se/assets/charlesforstman.png" alt="" />
          <div className="meta"><p className="name">{username}</p></div>
        </div>
      </li>
    )
  }
}



