import React, { Fragment } from 'react'
import { connect } from 'react-redux'
import { signUp } from './redux'

@connect(null, {signUp})
export class SignUp extends React.Component {
  state = {
    username: '',
    password: '',
  }

  changeInputState = (e, field) => {
    this.setState({
      [field]: e.target.value,
    })
  }

  signUp = () => {
    this.props.signUp(this.state.username, this.state.password)
  }

  render = () => (
    <div className="login-page">
      <div className="form">
        <div className="register-form">
          <input placeholder="username" value={this.state.username}
                 onChange={e => this.changeInputState(e, 'username')} />

          <input type="password" placeholder="password" value={this.state.password}
                 onChange={e => this.changeInputState(e, 'password')} />

          <button onClick={this.signUp}>register</button>
          <p className="message">Already registered?
            <span className='span-link' onClick={() => this.props.onSwitch(false)}>Sign In</span>
          </p>
        </div>
      </div>
    </div>
  )
}



