import React, { Fragment } from 'react'
import { Popup } from '../uikit/popup'
import { connect } from 'react-redux'
import { rename } from './redux'

@connect(state => ({
  user: state.user,
}))
export class RenameButton extends React.Component {
  state = {popup: false}
  showRenamePopup = () => {
    this.setState({popup: true})
  }

  close = () => {
    this.setState({popup: false})
  }

  render = () => (
    <Fragment>
      <button onClick={this.showRenamePopup}>change name</button>
      <Popup visible={this.state.popup} onClose={this.close}>
        <RenameForm user={this.props.user} onFinish={this.close} />
      </Popup>
    </Fragment>
  )
}

@connect(null, {rename})
export class RenameForm extends React.Component {
  constructor (props) {
    super(props)
    this.state = {username: this.props.user.username}
  }

  rename = () => {
    if (this.state.username !== this.props.user.username) {
      this.props.rename(this.state.username)
      this.props.onFinish()
    }
  }

  render = () => (
    <div className="form">
      <input placeholder="username" value={this.state.username}
             onChange={e => this.setState({username: e.target.value})} />

      <button onClick={this.rename}>Change name</button>
    </div>
  )
}



