import api from '../../service/api'

const APPEND_MESSAGE = '@@chat/APPEND_MESSAGE'
const FETCH_CONTACTS = '@@chat/FETCH_CONTACTS'
const SET_ACTIVE_CHAT = '@@chat/SET_ACTIVE_CHAT'

export const messagesReducer = (state = [], action = {}) => {
  switch (action.type) {
    case APPEND_MESSAGE:
      return [
        ...state,
        action.message,
      ]

    default:
      return state
  }
}

export const contactsReducer = (state = [], action = {}) => {
  switch (action.type) {
    case FETCH_CONTACTS:
      return action.contacts

    default:
      return state
  }
}

const ACTIVE_CHAT_INITIAL = {username: "public", id: "public"}
export const activeChatReducer = (state = ACTIVE_CHAT_INITIAL, action = {}) => {
  switch (action.type) {
    case SET_ACTIVE_CHAT:
      return action.user

    default:
      return state
  }
}

export const appendMessage = (message) => dispatch => {
  dispatch({type: APPEND_MESSAGE, message: message})
}

export const fetchContacts = () => dispatch => {
  api.contacts()
     .then(response => {
       dispatch({type: FETCH_CONTACTS, contacts: response.contacts})
     })
}

export const setActiveChat = (user) => dispatch => {
  dispatch({type: SET_ACTIVE_CHAT, user})
}



