import http from './http'

/**
 * Signin request.
 * @returns {*}
 * @param username
 * @param password
 */
const signIn = (username, password) => {
  return http.post(`/signin`, {username, password}).then(response => {
    http.setAuthHeader(response.token)
    return response
  })
}

/**
 * Signup request.
 * @returns {*}
 * @param username
 * @param password
 */
const signUp = (username, password) => {
  return http.post(`/signup`, {username, password}).then(response => {
    http.setAuthHeader(response.token)
    return response
  })
}

/**
 * Rename request.
 * @returns {*}
 * @param username
 */
const rename = (username) => {
  return http.post(`/api/rename`, {username})
}

/**
 * Contacts request.
 * @returns {*}
 */
const contacts = () => {
  return http.get(`/api/contacts`)
}

export default {
  signIn,
  signUp,
  rename,
  contacts,
}



