import React from 'react'
import { connect } from 'react-redux'
import { signIn } from './redux'
import { addNotification as notify } from 'reapop'

@connect(null, {signIn, notify})
export class SignIn extends React.Component {
  state = {
    username: '',
    password: '',
  }

  changeInputState = (e, field) => {
    this.setState({
      [field]: e.target.value,
    })
  }

  signIn = () => {
    this.props.signIn(this.state.username, this.state.password)
  }

  handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.signIn()
    }
  }

  render = () => (
    <div className="login-page">
      <div className="form">
        <div className="login-form">
          <input placeholder="username" value={this.state.username}
                 onKeyPress={this.handleKeyPress}
                 onChange={e => this.changeInputState(e, 'username')} />

          <input type="password" placeholder="password" value={this.state.password}
                 onKeyPress={this.handleKeyPress}
                 onChange={e => this.changeInputState(e, 'password')} />

          <button onClick={this.signIn}>login</button>
          <p className="message">Not registered?
            <span className='span-link' onClick={() => this.props.onSwitch(true)}>Create an account</span>
          </p>
        </div>
      </div>
    </div>
  )
}



